document.addEventListener('turbolinks:load', function() {
  console.log("Hola")
  const orderSelect = document.querySelector('select[name="order"]');
  
  // Intentamos obtener el valor del orden desde localStorage
  const savedOrder = localStorage.getItem('note_order');
  let orderToApply = savedOrder || null; // Si no hay valor guardado, lo tomamos de la URL

  // Revisa si hay un parámetro 'order' en la URL y lo aplica al select
  const urlParams = new URLSearchParams(window.location.search);
  const orderFromUrl = urlParams.get('order');
  if (orderFromUrl) {
    orderToApply = orderFromUrl;
  }

  // Si encontramos un valor de orden, lo aplicamos tanto al select como a la URL
  if (orderToApply) {
    // Aplicamos el valor al select
    orderSelect.value = orderToApply;

    // Si el valor del orden en la URL es diferente de lo que está en la URL actual, actualizamos la URL
    if (window.location.search.indexOf('order') === -1 || orderFromUrl !== savedOrder) {
      const currentUrl = new URL(window.location.href);
      currentUrl.searchParams.set('order', orderToApply); // Establecemos el parámetro 'order'
      window.history.pushState({}, '', currentUrl);  // Actualizamos la URL sin recargar la página
    }
  }

  // Cuando el usuario cambia el orden
  orderSelect.addEventListener('change', function() {
    const selectedValue = this.value;
    
    // Guardamos el valor en localStorage
    localStorage.setItem('note_order', selectedValue);

    // Actualizamos la URL para reflejar el cambio sin recargar la página
    const currentUrl = new URL(window.location.href);
    currentUrl.searchParams.set('order', selectedValue);  // Establece el parámetro 'order'
    window.history.pushState({}, '', currentUrl);  // Actualiza la URL sin recargar la página

    // Llamada AJAX o actualización de la vista sin recargar
    updateNotesOrder(selectedValue);  // Función que actualiza el orden de las notas sin recargar la página
  });

  // Función para actualizar las notas en la página sin recargarla
  function updateNotesOrder(order) {
    // Realizamos una solicitud AJAX para obtener las notas ordenadas
    fetch(`/notes?order=${order}`)
      .then(response => response.json())
      .then(data => {
        // Aquí, actualizas el contenido de las notas de acuerdo con la respuesta
        // Puedes re-renderizar las notas con la nueva información
        const notesContainer = document.getElementById('notes-container');
        notesContainer.innerHTML = '';  // Limpiar las notas actuales

        // Suponiendo que las notas vienen como una lista de objetos
        data.notes.forEach(note => {
          const noteElement = document.createElement('div');
          noteElement.textContent = note.title;  // Personaliza esto según tus datos
          notesContainer.appendChild(noteElement);
        });
      })
      .catch(error => console.error('Error al actualizar las notas:', error));
  }
});